@import url(https://fonst.googleapis.com/css?family=Montserrat:500);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{box-sizing:border-box;background:white;min-height:100vh}.container{overflow:auto;width:100%;max-width:100vw;margin:0rem auto;padding:0 2rem}:root{--ff-primary: 'Open Sans', sans-serif;--ff-secondary: 'Source Code Pro', monospace}body{min-height:100vh;height:auto !important;max-width:100vw;font-family:'Open Sans', sans-serif;font-family:var(--ff-primary)}h1{font-family:'Open Sans', sans-serif;font-family:var(--ff-primary)}h2{font-family:'Open Sans', sans-serif;font-family:var(--ff-primary)}p{line-height:2rem}input[type='text'],input[type='password']{margin-top:2em;width:98%;padding:12px 20px;margin:0.25em;display:inline-block;border:1px solid #ccc;border-radius:4px;box-sizing:border-box}input[type='submit']{padding:2em;width:98%;background-color:#69baa6;color:white;padding:14px 20px;margin:0.25em;border:none;border-radius:4px;cursor:pointer}input[type='submit']:hover{background-color:#518f7f}.btn-primary{padding:9px 25px;background-color:#20b2aa;border:none;border-radius:40px;cursor:pointer;transition:all 0.3s ease 0s}.btn-primary:hover{background-color:rgba(75,143,160,0.8)}.btn-secondary{padding:9px 25px;background-color:#518f7f;border:none;border-radius:40px;cursor:pointer;transition:all 0.3s ease 0s}.btn-secondary:hover{background-color:rgba(81,143,127,0.8)}a{color:#20b2aa !important}*{box-sizing:border-box}li,a,button{font-family:'Montserrat', sans-serif;font-weight:500;font-size:1rem;color:#edf0f1;text-decoration:none}.header{display:flex;flex-direction:row;justify-content:flex-end;align-items:center;padding:10px;background-color:#303030}.logo{cursor:pointer;margin-right:auto}@media (min-width: 600px){.nav__links{list-style:none}.nav__links li{display:inline-block;padding:0px 20px}.nav__links li a{color:#fff !important;transition:all 0.3s ease 0s}.nav__links li a:hover{color:rgba(255,255,255,0.8) !important}}@media (max-width: 600px){.nav__links{display:none}}.footer-container{display:flex;flex-direction:column}.footer{position:relative;background-color:#303030;color:white;text-align:center}.footer-fixed{position:fixed;padding:10px 10px 0px 10px;bottom:0;color:white;width:100%;height:4em;background-color:#303030}@media (min-width: 800px){.recipe-card{box-shadow:0 4px 8px 0 rgba(0,0,0,0.2);transition:0.3s;border:solid silver 1px;min-width:400px;max-width:400px;margin:1em;height:250px}.recipe-card p,.recipe-card h2,.recipe-card h3,.recipe-card h4{color:black}}@media (max-width: 800px){.recipe-card{box-shadow:0 4px 8px 0 rgba(0,0,0,0.2);transition:0.3s;border:solid silver 1px;width:90vw;margin:1em}}.recipe-card:hover{cursor:pointer;box-shadow:0 8px 16px 0 rgba(0,0,0,0.2)}.recipe-card-container{display:grid;grid-gap:2em;grid-template-columns:repeat(auto-fit, minmax(400px, 1fr));justify-items:center}.modal-background-active{position:fixed;width:100%;height:100vh;top:0;left:0;background-color:rgba(0,0,0,0.6);display:flex;justify-content:center;align-items:center;transition:visibility 0s, opacity 0.5s}.modal-background{visibility:hidden;opacity:0}@media (min-width: 800px){.modal{padding:1em;position:relative;background-color:white;border:solid #303030 1px;width:40%;height:40%;display:flex;justify-content:space-around;align-items:center;flex-direction:column}}@media (max-width: 800px){.modal{padding:1em;position:relative;background-color:white;border:solid #303030 1px;width:70%;height:50%;display:flex;justify-content:space-around;align-items:center;flex-direction:column}}.modal-close{position:absolute;top:10px;right:10px;font-weight:bold;cursor:pointer}@media (min-width: 600px){.login{display:flex;flex-direction:row}.login__img{overflow:hidden;object-fit:cover;max-height:100vh}.login__form{max-width:40vw;min-width:35vw}}@media (max-width: 600px){.login{display:inline-block}.login__img{overflow:hidden;object-fit:cover;max-height:100vh;max-width:100vw}.login__form{z-index:2}}.login h2{text-align:center}.sign-up-link{text-align:center}@media (min-width: 800px){.about-page h1{text-align:center}.about-page__content{display:flex;justify-content:space-around}.about-page__description{margin:2em}.about-page__contact{display:flex;flex-direction:column;min-width:300px;height:300px;color:white;background-color:#303030;padding:1em;margin:1em;align-items:center;justify-content:center}.about-page__contact h2{text-align:center}}@media (max-width: 800px){.about-page h1{text-align:center}.about-page p{margin:1em}.about-page__content{display:flex;flex-direction:column;justify-content:center;align-items:center}.about-page__contact{display:flex;flex-direction:column;color:white;background-color:#303030;padding:1em;margin:1em;align-items:center;justify-content:center}.about-page__contact h2{text-align:center}}.view-recipe h1,.view-recipe p{text-align:center}.view-recipe__header{display:flex;justify-content:center;margin:2em}.view-recipe__img{width:200px;height:200px;margin:0.5em;border:solid #20b2aa 5px;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2)}.view-recipe__content{display:flex;justify-content:space-evenly}.view-recipe__content li{color:black;line-height:2rem}

